import Link from 'next/link'
import users from '../data/users'

const Index = () => (
    <div>
      <p>Hello Next.js, this is your friend Brian from logrocket</p>
      {
          users.map((user,i) => (
            <Link key={i} href={`/user/${user.name}`}>
                <a>{`${user.name}`}</a>
            </Link>
          ))
      }
      
    </div>
  )
  export default Index